import React, { useState, Component } from "react";
import store from "../../state/store/index";
import { getWinningLosingProducts } from "../../helpers/api";

const guid = store.getState().guid;

export const CardWinning = () => {
    const [count, setCount] = useState("--");

    getWinningLosingProducts(guid, "winning")
        .then(function (products) {
            setCount(products.length);
        })
        .catch(function (error) {
            return error;
        });

    return (
        <div class="card bg-light mb-3">
            <div class="card-header">Winning</div>
            <div class="card-body text-success">
                <h5 class="card-title counter-total">{count}</h5>
            </div>
        </div>
    );
};

export const CardLosing = () => {
    const [count, setCount] = useState("--");

    getWinningLosingProducts(guid, "losing")
        .then(function (products) {
            setCount(products.length);
        })
        .catch(function (error) {
            return error;
        });

    return (
        <div class="card bg-light mb-3">
            <div class="card-header">Losing</div>
            <div class="card-body text-danger">
                <h5 class="card-title counter-total">{count}</h5>
            </div>
        </div>
    );
};
